* {
  box-sizing: border-box;
  margin: 0;
}

html, body {
  height: 100%;
}

#root {
  height: 100%;
  background-color: #fdfdfd;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.page-header {
  padding: 3% 3% 2%;
  display: flex;
}

.page-header-left, .page-header-center, .page-header-right {
  align-items: center;
  display: flex;
}

.page-header-left {
  flex: 1;
  justify-content: flex-start;
}

.page-header-center {
  flex: 2;
  justify-content: center;
}

.page-header-right {
  flex: 1;
  justify-content: flex-end;
}

.page-title {
  color: #0f0f0f;
  text-transform: uppercase;
  font-family: Palatino, Verdana, Times New Roman;
  font-size: 40px;
  line-height: .8;
}

.instagram-icon {
  vertical-align: middle;
}

.page-content {
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  padding: 0 10%;
  display: flex;
}

.about {
  text-align: center;
  font-family: Palatino, Verdana, Times New Roman;
  font-size: 20px;
}

.page-footer {
  text-transform: uppercase;
  justify-content: center;
  padding: 30px;
  font-family: Palatino, Verdana, Times New Roman;
  font-size: 14px;
  display: flex;
}

@media (max-width: 480px) {
  #root {
    justify-content: space-around;
  }

  .page-header-left, .page-header-right {
    display: none;
  }

  .page-title {
    font-size: 32px;
  }

  .about {
    font-size: 16px;
  }
}

